import React, { useRef, useEffect } from "react";
import gsap from "gsap";

export default function SecondNav({ clickHandler }) {
  const wrapper = useRef(null);
  const tl = gsap.timeline();
  useEffect(() => {
    const current = wrapper.current.children;
    let offset = -90;
    let scope = 180;
    let perStep = -(scope / (current.length - 1));
    for (let i = 0; i < current.length; i++) {
      const element = current[i];
      tl.to(element, { rotate: offset + perStep * i + "deg" });
      for (let index = 0; index < element.children.length; index++) {
        const child = element.children[index];
            tl.to(child, { rotate: -(offset + perStep * i) * Math.pow(index + 1, index) + "deg" });
            if(i === 1 || i === 3) tl.to(child, { rotate:'180deg'});
      }
    }
  });

  return (
    <div ref={wrapper} className="SecondNav">
      <button
        onClick={() =>
          clickHandler(<i class="fas fa-smile-wink">Ustawienia</i>)
        }
      >
        <span><i class="fas fa-cog"></i></span>
      </button>
      <button
        onClick={() =>
          clickHandler(<i class="fas fa-smile-wink">Produkty second hand</i>)
        }
      >
        <span>Second Hand</span>
      </button>
      <button
        onClick={() =>
          clickHandler(<i class="fas fa-smile-wink">Produkty Outlet</i>)
        }
      >
        <span>Outlet</span>
      </button>
      <button
        onClick={() =>
          clickHandler(<i class="fas fa-smile-wink">Produkty dla Vintage</i>)
        }
      >
        <span>Vintage</span>
      </button>
      <button
        onClick={() => clickHandler(<i class="fas fa-smile-wink">Log-In</i>)}
      >
        <span><i class="fas fa-sign-in-alt"></i></span>
      </button>
    </div>
  );
}
